import sunUiUpimg from '@/components/sunui-upimg/sunui-upimg.vue';
import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
export default {
	data() {
		return {
			text: "",
			picUrl: "",
			skin: false,
			addressshow: "点击右侧按钮添加位置",
			address: "",
			fieldStr: "选择类型",
			field: "",
			modalName: null,
			checkbox: [{
				value: 0,
				name: '娱乐',
				alias: 'funny',
				checked: 0,
			}, {
				value: 1,
				name: '动漫',
				alias: 'anime',
				checked: 0,
			}, {
				value: 2,
				name: '新闻',
				alias: 'news',
				checked: 0,
			}, {
				value: 3,
				name: '时尚',
				alias: 'fashion',
				checked: 0,
			}, {
				value: 4,
				name: '运动',
				alias: 'motion',
				checked: 0,
			}, {
				value: 5,
				name: '科技',
				alias: 'science',
				checked: 0,
			}]
		}
	},
	components: {
		'sunui-upimg': sunUiUpimg
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		SwitchSex(e) {
			let _this = this
			if(this.skin == false){
				uni.chooseLocation({
				    success: function (res) {
				        console.log('位置名称：' + res.name);
				        console.log('详细地址：' + res.address);
				        console.log('纬度：' + res.latitude);
				        console.log('经度：' + res.longitude);
						_this.addressshow = res.address
						_this.address = res.address
						_this.skin = e.detail.value
				    },fail() {
						_this.skin = e.detail.value
				    	_this.addressshow = "获取位置失败，请取消并重试"
				    }
				});
			}else{
				_this.skin = e.detail.value
				_this.addressshow = "点击右侧按钮添加位置"
			}
			
		},
		getImageInfo1(e) {
			console.log('图片返回1：', e)
			this.picUrl = e
			console.log(this.picUrl)
		},
		ChooseCheckbox(e) {
			let temp = '';
			let items = this.checkbox;
			let values = e.currentTarget.dataset.value;
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
				if (items[i].value == values) {
					if(items[i].checked == 1){
						this.fieldStr = "选择类型"
						items[i].checked = 0
					}else{
						items[i].checked = 1
					}
				}else{
					items[i].checked = 0
				}
			}
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
				if(items[i].checked == 1){
					this.fieldStr = items[i].name
					this.field = items[i].alias
					break;
				}
			}
		},
		send(){
			let _this = this
			var registerRule = [{
					name: 'text',
					checkType: 'notnull',
					checkRule: '',
					errorMsg: '说些有趣的事情吧'
				},
				{
					name: 'text',
					checkType: 'string',
					checkRule: '0,254',
					errorMsg: '您想说的超过254个字符了哦，删减些吧'
				}
			];
			var checkRes = graceChecker.check(_this, registerRule);
			if(!checkRes){
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}else{
				uni.request({
				    url: this.Server_IP + 'addSay', //仅为示例，并非真实接口地址。
				    data: {
						userId: uni.getStorageSync('userId'),
						text: _this.text,
						address: _this.address,
						field: _this.field,
						picUrl: JSON.stringify(_this.picUrl),
						userName: uni.getStorageSync('userName')
				    },
				    header: {
				        'custom-header': 'addSay' //自定义请求头信息
				    },
					method:"POST",
					dataType:"json",
				    success: (res) => {
				        console.log(res.data);
						if(res.data.info.code == '0'){
							uni.navigateBack({
								delta:1
							})
							uni.showToast({
								title: "发表成功",
								icon: "none"
							});
						}else{
							console.log(res.data.info.message);
							uni.showToast({
								icon: 'none',
								title: res.data.info.message
							});
						}
				    },
				});
			}
		}
	}
}
